.intro {
    padding: 4rem calc((100vw - 1200px) / 2);
    background-color: lightgray;
}

.intro_container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 30px;
    gap: 30px;
    font-size: 13.8px;
    padding: 1rem;
}

.intro_heading {
    color: #00854A;
    text-align: center;
    margin-bottom: 3rem;
    font-size: clamp(1.7rem, 2.2vw, 2.7rem);
}

.intro_heading i{
    font-size: clamp(1.5rem, 1.8vw, 2.4rem);
}

.intro_img {
    width: 100%;
    height: 100%;
    border: 2px solid green;
    border-radius: 10px;
    cursor: pointer;
}

.intro_feature {
    font-weight: 700;
    margin-bottom: 1.5rem;
    font-size: clamp(1rem, 5vw, 1.2rem);
}


.fa-window-close {
    font-size: 25px;
    cursor: pointer;
}

.overlay-styles {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 1000;
}

.modal-styles {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #ffff;
    padding: 50px;
    z-index: 1000;
    border: '3px solid black';
    border-radius: 10px;
}

@media screen and (max-width: 1024px) {
    .intro_container {
        grid-template-columns: 1fr;
    }
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}

.images-container {
    position: absolute;
    top: 250px;
}

.images-title{
    margin-top: 80px;
    color: gray;
}

.images-row {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.images-row .images-column{
    flex: 33.3% 1;
    width: auto;
    overflow: hidden;
    padding: 0 4px;
    height: 100%;
}

.images-row .images-column img {
    border: 3px solid black;
    margin-top: 2%;
    width: 100%;
    height: 100%;
    cursor: pointer;
    border-radius: 5px;
    transition: 0.3s linear;
}

@media only screen and (max-width: 800px) {
    .images-row .images-column {
        flex: 50% 1;
    }
    .images-container {
        position: absolute;
        top: 220px;
    }
}

@media only screen and (max-width: 600px) {
    .images-row .images-column {
        flex: 100% 1;
    }

    .images-container {
        position: absolute;
        top: 200px;
    }

    .images-title {
        margin-top: 30px;
    }
}
/* Main CONTACTINFO CSS */

.contactinfo {
    display: grid;
    grid-template-rows: 1fr 1fr;
    font-size: medium;  
    padding: 0;
    margin: 0;
}

/* Plot Details Container */
.plot-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 50px;
    gap: 50px;
    padding: 20px;
    background-color: #00854B;
}

.layout {
    padding: 10px;
    width: 100%;
    height: auto;
}

.payment {
    padding: 10px;
    width: 100%;
    height: auto;
}

.layout h2 {
    font-size: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
}

.payment h2 {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 25px;
    text-align: center;
    color: white;
}

.payment h3 {
    font-size: 18px;
    font-weight: 500;
    color: white;
    margin: 0;
    padding-top: 25px;
    padding-bottom: 10px;
}

.layout h3 {
    font-size: 18px;
    font-weight: 500;
    color: white;
    margin: 0;
    padding-top: 25px;
    padding-bottom: 20px;
    padding-left: 100px;
}

.layout li {
    font-size: 14px;
    color: white;
    margin: 0;
    padding-top: 5px;
    padding-left: 100px;
