}

/* ADDRESS DETAILS CONTAINER */
.address-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 40px;
    gap: 40px;
    /* background-color: lightgrey; */
    padding: 20px;
}

.address-details {
    width: 100%;
    height: auto;
