.hs-container {
  display: grid;
  padding: 50px;
  padding-bottom: 100px;
  grid-template-columns: 1fr 1fr;
}

.hs-heading {
  font-weight: 800;
  font-family: var(--font);
  font-size: 60px;
  text-align: left;
  padding-bottom: 30px;
  padding-top: 50px;
  line-height: 60px;
  color: #00854b;
}

.hs-p {
  font-weight: 800;
  line-height: 29px;
  font-stretch: ultra-expanded;
  color: #00854b;
}

.hs-buttons {
  padding-top: 25px;
}

.hs-buttons button {
  color: white;
  font-size: 0.95rem;
  background-color: #00854b;
  margin-right: 30px;
  border: none;
  height: 6.45vh;
  border-radius: 20px;
  width: 195px;
  cursor: pointer;
}

.hs-image {
  height: 60vh;
  width: 40vw;
}

.hs-image {
  animation: up-down 1.5s ease-in-out infinite alternate-reverse both;
}

@keyframes up-down {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}

.hs-img-container {
  padding-top: 40px;
}

